import {
  Component,
  NgModule,
  ViewEncapsulation,
  Input,
  forwardRef
} from '@angular/core'
import {
  FormsModule,
  NG_VALUE_ACCESSOR,
  ControlValueAccessor
} from '@angular/forms'
import { CommonModule } from '@angular/common'
import { XTipModule } from '../tip'
import { NgModelBase } from '../utils'

const CHECKLIST_CONTROL_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => XChecklist),
  multi: true
}

export interface IChecklistOptions {
  key: any
  value: any
}

@Component({
  selector: 'x-checklist',
  encapsulation: ViewEncapsulation.None,
  template: require('./checklist.html'),
  styles: [
    require('./checklist.less')
  ],
  providers: [
    CHECKLIST_CONTROL_VALUE_ACCESSOR
  ]
})
export class XChecklist extends NgModelBase implements ControlValueAccessor {
  @Input() title: string = 'Checklist'
  @Input() options: IChecklistOptions[] = []
}

@NgModule({
  declarations: [
    XChecklist
  ],
  imports: [
    FormsModule,
    CommonModule,
    XTipModule
  ],
  exports: [
    XChecklist,
    FormsModule,
    CommonModule,
    XTipModule
  ]
})
export class XChecklistModule { }



